import { Component, OnInit } from '@angular/core';

@Component({
	selector: 'app-car',
	templateUrl: './car.component.html',
	styleUrls: ['./car.component.sass']
})
export class CarComponent implements OnInit {
	
	car: {
		name:		string,
		speed:	number,
		model:	string,
		image: string,
		color:	{
			salon:	string,
			car:		string,
			weels:	string
		},
	}
	options: string[];
	test: number;
	isEdit:boolean = false;

	showEdit(){
		this.isEdit = !this.isEdit
	}
	
	ngOnInit() {
		this.car = {
			name: 'Lexus',
			model: 'rx300',
			speed: 230,
			image: 'https://images.pexels.com/photos/2684218/pexels-photo-2684218.jpeg?auto=compress&cs=tinysrgb&dpr=2&h=750&w=1260',
			color: {
				salon: 'Чёрный',
				car: 'Белый',
				weels: 'Заводские'
			},
		},
		this.options = [" ABS", " AutoPilot", " AutoParking"],
		this.test = 1;
	}
	addOpt(option){
		this.options.unshift(option);
		return false;
	}
	deleteOpt(option){
		for(let i = 0; i < this.options.length; i++){
			if(this.options[i] == option){
				this.options.splice(i, 1);
				break;
			}
		}
	}
	bmwSelect(){
		this.car = {
			name: 'BMW',
			model: 'X5',
			speed: 260,
			image: 'https://images.pexels.com/photos/2127012/pexels-photo-2127012.jpeg?auto=compress&cs=tinysrgb&dpr=2&h=750&w=1260',
			color: {
				salon: 'Серебристый',
				car: 'Чёрный',
				weels: 'Заводские'
			},
		},
		this.options = [" ABS", " AutoPilot", " AutoParking"],
		this.test = 1;
	}
	audiSelect(){
		this.car = {
			name: 'Audi',
			model: 'R8',
			speed: 320,
			image: 'https://images.pexels.com/photos/909907/pexels-photo-909907.jpeg?auto=compress&cs=tinysrgb&dpr=2&h=750&w=1260',
			color: {
				salon: 'Чёрный',
				car: 'Белый',
				weels: 'Заводские'
			},
		},
		this.options = [" ABS", " AutoPilot", " AutoParking"],
		this.test = 1;
	}
	mercedesSelect(){
		this.car = {
			name: 'Mercedes',
			model: 's 925',
			speed: 360,
			image: 'https://images.pexels.com/photos/241316/pexels-photo-241316.jpeg?auto=compress&cs=tinysrgb&dpr=2&h=750&w=1260',
			color: {
				salon: 'Серый',
				car: 'Хамелион',
				weels: 'Заводские'
			},
		},
		this.options = [" ABS", " AutoPilot", " AutoParking"],
		this.test = 1;
	}
}

